import { Block, BlockChildren } from '@ali/4ever-cangjie';
import type { ParagraphProperties, ParagraphChildren } from '@ali/4ever-plugin-paragraph';
import { FoldableHeadingData } from './heading1';

export default class Heading4 extends Block<FoldableHeadingData, ParagraphChildren> {

  static TYPE = 'heading-4';

  static create<SD = FoldableHeadingData, SN extends BlockChildren = ParagraphChildren>(
    properties: Heading4 | ParagraphProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`Heading4.create` does not accept string');
    }
    if (Heading4.isHeading4(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: Heading4.TYPE,
    });
  }

  static isHeading4(maybeHeading4: any): maybeHeading4 is Heading4 {
    return Block.isBlock(maybeHeading4) && maybeHeading4.type === Heading4.TYPE;
  }

}
